@import url(https://fonts.googleapis.com/css?family=Archivo+Black|Inconsolata|News+Cycle);

//Color Variables
$color1: #000001; //Black
$color2: #ffffff; //white
$color3: #ffffff; //white

// Base Styles
*,
*:before,
*:after {
    box-sizing: inherit;
}
html {
    box-sizing: border-box;
}

body {
  background-color: $color2;
  color: $color1;
  font-family: 'News Cycle', sans-serif;
  margin-left: 10%;
  margin-right: 10%;
}

h1 {
    color: #000001;
    font-family: 'News Cycle', sans-serif;
    font-size: 40px;
    line-height: 40px;
    vertical-align: top;
    margin-top: 0;
}

p {
    color: #000001;
    font-family: 'Inconsolata', serif, monospace;
    font-size: 14px;
    line-height: 20px;
    vertical-align: top;
    margin-top: 0;
}

.navBar {
  background-color: $color2;
  position: relative;
  min-width: 100%;
  padding-top:1em;
}

.logo { 
    background-image: url("./Ico_KC_logo.gif");
    user-select: none;
    background-size: 350px 88px;
    background-repeat: no-repeat;
    width: 350px;
    height: 88px;
    display: inline-block;
}

.dashedline {
    border-bottom: 1px dashed #000001;
    padding-top: 1em;
    margin-bottom:2.5em;
}

.contentbox {
    max-width: 942px;
    margin: auto;
}

.copyrightdiv {
    text-align: center;
    width: 100%;
    padding-top: 40px;
    padding-bottom: 20px;
}

// Content Wrapper

.navwrapper {
  margin: 0 auto;
  max-width: 942px;
  padding: 0%;
}

/*.wrapper {
  margin: 0 auto;
  max-width: 942px;
  padding: 0 2%;
}*/

// Full Navigation, including mobile menu
nav {
  ul {
    display: flex;
    justify-content: space-between;
    list-style-type: none;
    width: 40%;
    
    a {
      color: $color1;
      text-decoration: none;
      transition: all .5s ease;

      &:hover {
        background-color: $color1; 
        color:$color2;
        padding-right: 10px ;
        padding-left: 10px ;
        padding-top: 5px ;
        padding-bottom: 5px ;
      }
    }
  }
  
  li  {
    display: inline-block;
  }
  
  #menu-toggle {display: none;}
  
  .label-toggle {display: none;}
  
  /*.wrapper {
    align-items: center;
    display: flex;
    justify-content: space-between;
  }*/

    .navwrapper {
    align-items: center;
    display: flex;
    justify-content: space-between;
  }
  
  @media screen and (max-width: 800px) {
     .logo {
        background-image: url("./Ico_KC_logo.gif");
        user-select: none;
        background-size: 300px 75px;
        background-repeat: no-repeat;
        width: 300px;
        height: 75px;
        display: inline-block;}
    nav {
      ul {
        a {
          color: $color2;
          &:hover {
      
        padding:0px;
      }
        }
      }
    }
    ul {
      background-color: $color1;
      display: block;
      height: 0;
      list-style-type: none;
      opacity: 0;
      text-align: center;
      transition: all 1s ease;
      width: 100%;
      visibility: hidden;
      margin-top:10px;
    }
    
    li {
      border-bottom: 1px dashed $color2;
      color: $color2;
      display: block;
      font-size: 1.5em;
      padding: 0.5em 0;
    }
  
    #menu-toggle:checked ~ ul {
      opacity: 1;
      height: auto;
      visibility: visible;
    }
    
    .label-toggle {
      background: linear-gradient(to bottom, $color1 0%, $color1 20%, transparent 20%, transparent 40%, $color1 40%, $color1 60%, transparent 60%, transparent 80%, $color1 80%, $color1 100%); //Gradient Hamburger
      cursor: pointer;
      display: block;
      float: right;
      height: 20px;
      margin-top: 1.8em;
      width: 30px;
    }
    
    /*.wrapper {display: block;}*/
    .navwrapper {display: block;}
  }

  @media screen and (max-width: 500px) { 
        .logo {
        background-image: url("./Ico_KC_logo_mob.gif");
        background-size: 200px 50px;
        width: 200px;
        height: 50px;
        display: inline-block;
        margin-top: 0.5em;
    }
     .label-toggle {     
      margin-top: 1.4em;
    }
  }
  
}

